import React, {useState, useEffect} from "react";
import CarouselHome from "./HomeComponents/Carousel_Home";
import ImageCarousel from "./HomeComponents/imageCarousel";
import datas from "../../Datas/datasets";
export default function Home() {
    const [matches, setMatches] = useState(
        window.matchMedia("(min-width: 768px)").matches
      )
    
      useEffect(() => {
        window
        .matchMedia("(min-width: 768px)")
        .addEventListener('change', e => setMatches( e.matches ));
      }, []);
    return (
        <div>
            <ImageCarousel />
            {matches? <CarouselHome datas={datas} title="Discounts for You" /> : null}
            
        </div>
    );
};